                  .doc()
                  .set({
                    froms: that.$store.state.users.id,
                    status: 2,
                    ...this.vdata,
                  })
                  .then((res) => {
                    sdb
                      .collection("tbuser")
                      .doc()
                      .select(`select * from tbuser where id='${tos.id}'`)
