    //   .get()
    //   .then(res => {
    //     let data = res.docs.map(e => e.data());
    //     this.$forceUpdate()
    //     if (data.length > 0) {
    //       that.$router.push("/lembur/status");
    //     } else {
    //       // that.ready = true;
    //     }
